.quiz-container {
  padding: 1rem;
  display: flex;
  justify-content: center;
  
  background: rgba(0, 0, 0, 0.15);
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(6px);

.Quiz__question {
  font-size: 1.2rem;  
}
  
  .Quiz__radioButtonContainer {
    margin-top: 2rem;

    .Quiz__radioButton {
      // display: none;

    font-size: 1.2rem;
    padding: 1rem 1.5rem;
    background: rgba(0, 0, 0, 0.35);
    color: white;
    border-radius: 10px;
    backdrop-filter: blur(15px);
    -webkit-backdrop-filter: blur(15px);
    border: 1px solid rgba(148, 103, 16, 1);
    cursor: pointer;
    transition: box-shadow 0.2s ease-in-out;
    
    &:hover,
    &:active,
    &:focus {
      box-shadow: 0px 0px 10px rgb(0, 222, 252);
    }
    }

    
  }
  
  .Quiz__radioButton-label {
    display: none;

    font-size: 1.2rem;
    padding: 1rem 1.5rem;
    background: rgba(0, 0, 0, 0.35);
    border-radius: 10px;
    backdrop-filter: blur(15px);
    -webkit-backdrop-filter: blur(15px);
    border: 1px solid rgba(148, 103, 16, 1);
    cursor: pointer;
    transition: box-shadow 0.2s ease-in-out;
    
    &:hover,
    &:active,
    &:focus {
      box-shadow: 0px 0px 10px rgb(0, 222, 252);
    }
  }
  
  // Code as basis for the gradient button taken from https://stackdiary.com/gradient-hover-effect-css/
  .Quiz__gradientButton {
    padding: 0.875em 2em;
    margin-top: 50px;
    background: rgba(0, 0, 0, 0.35);
    border-radius: 10px;
    backdrop-filter: blur(15px);
    -webkit-backdrop-filter: blur(15px);
    border: 1px solid rgba(148, 103, 16, 1);
    font-family: inherit;
    color: #fff;
    font-size: 1.2rem;
    cursor: pointer;
    transition: box-shadow 0.2s ease-in-out;
    
    &:hover,
    &:active,
    &:focus {
      box-shadow: 0px 0px 10px rgb(0, 222, 252);
    }
  }
  
}
